
import {axiosGet} from  "../../utils/request"
import { message } from 'antd';
export const resourceFun=(params,cb)=>{
    return (dispatch)=>{
        return axiosGet(params).then(res=>{
            // console.log('', res);
            if(res.status==200){
                dispatch({
                    type: "RESOUSE",
                    resouseData: res.data.data
                });
                cb()
            }else {
                message.error(res['data']['message'])
            }

        }).catch((response) => {
        });;
    }
}
